@charset "UTF-8";
html {
  height: 100%;
}

body {
  height: 100%;
  margin: 0;
  padding: 0;
  min-width: 280px;
  font-family: "PT Sans", sans-serif;
  color: #052E4C;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

input,
textarea,
button {
  font: inherit;
}

.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  padding-left: 20px;
  padding-right: 20px;
}

@media (min-width: 768px) {
  .container {
    margin: 0 auto;
  }
}
@media (min-width: 1118px) {
  .container {
    width: 1120px;
    margin: 0 auto;
    padding-left: 0;
    padding-right: 0;
  }
}
.header {
  width: 100%;
  /*display: flex;*/
  margin: 0 auto;
  box-sizing: border-box;
  /*background-color: #f5f1ee;
  overflow: hidden;
  color:#052E4C;
  background-image: url("../img/bg-banner.jpg");
  background-repeat: no-repeat;
  background-position: 30% bottom;
  background-size: auto; */
  outline: 1px solid blue;
}

.header__slide {
  display: flex;
  overflow: hidden;
  padding-bottom: 190px;
  background-color: #f5f1ee;
  background-repeat: no-repeat;
  background-size: auto;
  outline: 1px solid black;
}

.header__slide--first {
  background-image: url("../img/bg-banner.jpg");
  background-position: 30% bottom;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .header__slide--first {
    background-image: url("../img/bg-banner@2x.jpg");
    background-size: auto 508px;
  }
}

.header__slide--second {
  background-image: url("../img/bg-banner-4.jpg");
  background-position: 70% bottom;
}

.header__slide--third {
  background-image: url("../img/bg-banner-3.jpg");
  background-position: 20% bottom;
}

.header__container {
  outline: 3px solid red;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.header__promo {
  width: 100%;
  padding-top: 48px;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
}

.header__discription {
  width: 336px;
}

.header__send {
  margin-left: auto;
  position: relative;
  cursor: pointer;
}
.header__send::before {
  content: "";
  position: absolute;
  top: 0;
  right: -50px;
  width: 40px;
  height: 40px;
  background-color: #fff;
  border-radius: 50%;
  z-index: 1;
}
.header__send::after {
  content: "";
  position: absolute;
  top: 0;
  right: -50px;
  width: 40px;
  height: 40px;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../img/icon-busket.svg");
  z-index: 5;
}

.header__title {
  font-size: 42px;
  line-height: 42px;
  font-weight: 300;
  font-family: "Cormorant";
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 25px;
  padding: 0;
}
.header__title span {
  font-style: italic;
}
.header__title span:first-child {
  display: block;
}

.header__discription {
  font-size: 16px;
  line-height: 21px;
  display: block;
  padding: 0;
  margin-top: 0;
  margin-bottom: 35px;
}

.header__slider .slick-dots {
  bottom: 23px;
  text-align: left;
  padding-left: 20px;
  box-sizing: border-box;
}

.header .slick-dotted.slick-slider {
  margin-bottom: 0;
}

.header__slider .slick-dots li {
  width: 10px;
  height: 10px;
  margin-left: 2px;
}

.header__slider .slick-dots li button {
  width: 10px;
  height: 10px;
  background-color: #fff;
}

.header__slider .slick-dots li.slick-active button {
  background-color: #B84C74;
  width: 10px;
  height: 10px;
}

.header .slick-prev {
  top: auto;
  bottom: 50px;
  left: calc((100vw - 1120px) / 2);
  z-index: 10;
}

.header .slick-next {
  top: auto;
  bottom: 50px;
  left: calc((100vw - 1120px) / 2 + 30px);
}

@media (min-width: 768px) {
  .header__slide {
    box-sizing: border-box;
    min-height: 100vh;
    background-position: center bottom;
    background-size: cover;
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  .header__slide--first {
    background-size: 100% auto;
  }
}
@media (min-width: 768px) {
  .header__title {
    font-size: 78px;
    line-height: 78px;
    width: 846px;
  }
}
@media (min-width: 768px) {
  .header__discription {
    width: 477px;
    margin-bottom: 45px;
  }
}
@media (min-width: 1118px) {
  .header__slider {
    margin-top: -105px;
  }
  .header__promo {
    width: 1120px;
    margin: 0 auto;
    padding-left: 0;
    padding-right: 0;
    padding-top: 150px;
  }
  .header .slick-prev::before {
    content: "";
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: 13px;
    height: 24px;
    opacity: 1;
    background-repeat: no-repeat;
    background-image: url("../img/arrow-left.svg");
  }
  .header .slick-next {
    left: calc((100vw - 1120px) / 2 + 60px);
  }
  .header .slick-next::before {
    content: "";
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: 13px;
    height: 24px;
    opacity: 1;
    background-repeat: no-repeat;
    background-image: url("../img/arrow-right.svg");
  }
  .menu--opened .header__promo {
    transform: translateX(72px);
    position: static;
  }
}
.main-nav {
  position: relative;
  background-color: #fff;
}

.menu-opened .main-nav {
  position: static;
  transform: translateX(242px);
}

.main-nav__menu-wrapper {
  display: none;
  padding-left: 24px;
  padding-right: 24px;
  padding-bottom: 20px;
  background: #FCFCFC;
}

.menu--opened .main-nav__menu-wrapper {
  display: block;
}

.main-nav__logo-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 24px;
  padding-right: 24px;
  box-sizing: border-box;
  background: #FCFCFC;
  position: relative;
}

.main-nav__logo {
  display: block;
  padding-top: 15px;
  padding-bottom: 12px;
  text-align: center;
  cursor: pointer;
}

.main-nav__logo-add {
  display: none;
}

.main-nav__busket {
  display: inline-block;
  position: relative;
  cursor: pointer;
  width: 40px;
  height: 40px;
}
.main-nav__busket::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 40px;
  height: 40px;
  background-color: #fff;
  border-radius: 50%;
  z-index: 1;
}
.main-nav__busket::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 40px;
  height: 40px;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../img/icon-busket.svg");
  z-index: 5;
}

.main-nav__list {
  margin: 0;
}

.main-nav__toggle {
  position: relative;
  display: block;
  width: 24px;
  height: 24px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 2px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #417A82;
  box-shadow: 0 8px 0 0 #417A82, 0 16px 0 0 #417A82;
}

.main-nav__toggle--active::before,
.main-nav__toggle--active::after {
  content: "";
  position: absolute;
  top: 10px;
  left: 2px;
  width: 24px;
  height: 2px;
  background-color: #417A82;
}

.main-nav__toggle--active::before {
  transform: rotate(45deg);
  transition: transform 0.3s ease;
  box-shadow: none;
}

.main-nav__toggle--active::after {
  transform: rotate(-45deg);
  transition: transform 0.3s ease;
  box-shadow: none;
}

.main-nav__address {
  padding-top: 20px;
  text-align: center;
}

.main-nav__name {
  padding: 0;
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 10px;
  line-height: 12px;
}

.main-nav__phone {
  display: block;
  color: #417A82;
  font-size: 13px;
  line-height: 17px;
}

.main-nav__link-wrap .button {
  display: none;
}

@media (min-width: 768px) {
  .main-nav__logo {
    display: none;
  }
  .main-nav__logo-wrapper {
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .main-nav__link-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .main-nav__link-wrap .button {
    display: block;
    background-color: #B84C74;
    color: #fff;
    margin-right: 20px;
  }
  .main-nav__link-wrap .button:hover {
    color: #B84C74;
    background-color: #fff;
  }
}
@media (min-width: 1118px) {
  .main-nav__logo-wrapper {
    position: relative;
    width: 1120px;
    margin: 0 auto;
    padding-left: 0;
    padding-right: 0;
    padding-top: 30px;
    padding-bottom: 30px;
    background-color: transparent;
    z-index: 50;
  }
  .menu--opened .main-nav__logo-add {
    display: block;
    padding-top: 6px;
    padding-bottom: 5px;
    width: 30px;
    height: 19px;
    border-radius: 50%;
    color: #fff;
    text-transform: uppercase;
    font-size: 10px;
    text-align: center;
    background-color: #B84C74;
  }
  .menu--opened .main-nav__menu-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    padding-top: 35px;
    height: 100vh;
    overflow-y: auto;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
    width: 72px;
    z-index: 100;
  }
  .menu--opened .main-nav__toggle {
    transform: translateX(72px);
  }
  .menu--opened .main-nav__address {
    display: none;
  }
  .menu--opened .main-nav__list--contacts {
    display: none;
  }
}
