.product {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 460px;
  box-sizing: border-box;
  padding-top: 15px;
  padding-left: 7px;
  padding-right: 7px;
}

.product__fig {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 10;
}
.product__fig img {
  display: block;
  margin-bottom: 28px;
  background-color: #B84C74;
}

.product__figcaption {
  font-size: 16px;
  line-height: 20px;
  width: 200px;
}

.product__figcaption, .product__price {
  max-width: 100%;
  margin-top: auto;
}

.product__price {
  font-family: "Cormorant";
  font-style: italic;
  margin-bottom: 15px;
  z-index: 10;
}

.product__price-new {
  display: inline-block;
  margin-left: 10px;
  font-size: 32px;
  line-height: 38px;
  font-style: normal;
  font-weight: 700;
}

.product__price-old {
  font-size: 32px;
  line-height: 38px;
  display: inline-block;
  margin-left: 20px;
  color: #052E4C;
  opacity: 0.4;
  position: relative;
}
.product__price-old::before {
  content: "";
  position: absolute;
  top: 20px;
  left: 0;
  right: 0;
  border: 1px solid #B84C74;
  transform: rotate(-29.22deg);
}

.product__busket {
  border: 1px solid #052E4C;
  position: relative;
  cursor: pointer;
  width: 70%;
  z-index: 10;
}
.product__busket::before {
  content: "";
  position: absolute;
  top: 5px;
  right: -50px;
  width: 38px;
  height: 38px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../img/icon-busket.svg");
  z-index: 5;
}

.product__link {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: transparent;
}
.product__link:hover {
  background-color: #E3F6F5;
}

@media (min-width: 1118px) {
  .product {
    height: 533px;
